<template>
  <main id="main">
      <section>
          <SkillList :data="data" :login="login"/>
          <PortFolio :data="data" :login="login"/>
      </section>
    <MyInfo v-if="width < 1030" :data="data" :login="login"/>
  </main>
</template>

<script>

import SkillList from "./skillList.vue"
import PortFolio from "./portfolio.vue"
import MyInfo from "./myInfo.vue"

export default {
    name:"index_main",
    data(){
        return {
            width:0,
        }
    },
    components:{
        SkillList,
        PortFolio,
        MyInfo,
    },
    props:{
        data:Array,
        login:Number
    },  
    mounted(){
      window.addEventListener("resize",function(){
          this.width=window.innerWidth;
          console.log(this.width)
      })

    }      
}
</script>

<style scoped>
#main {display: flex; -webkit-box-pack: justify; justify-content: space-between; flex-wrap: wrap; }

#main section { width: 630px;}

@media all and (max-width:1030px){
    #main section {width: 100%;}
}
</style>